import { Component, OnInit } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map'
import { HttpReqDemoService } from '../../service/http-resquest.service';

@Component({
    selector: 'http-req-try',
    template: `
    <div class='col-md-4 border-common'>
        <h4>Demo HTTP request</h4>
        <p>{{title1}}</p><br>
        <p>{{title2}}</p>
    </div>
    `,
    providers: [HttpReqDemoService]
})

export class HTTPReqTryComponent implements OnInit {
    constructor(
        private http: Http,
        private httpSv: HttpReqDemoService
    ) { }

    title1 = '';
    title2 = '';

    ngOnInit() {
        this.http.get('http://ip.jsontest.com')
            // this.http.get('https://en.wikipedia.org/w/api.php?action=query&titles=Đức%20Page&prop=revisions&rvprop=content&format=json')
            .map(res => res.json())
            .subscribe(
            res => {
                this.title1 = res.ip;
            },
            err => {
                console.log(err)
            }
            )
        this.httpSv.getData('http://ip.jsontest.com')
            .subscribe(
            req => {
                this.title2 = req.ip;
            },
            err => {
                console.log(err)
            }
            )
    }
}


