import { Component, OnInit } from '@angular/core';

@Component({
    selector: 'child-viewchild-component',
    template: `
        <p>Value: {{value}}</p>
    `
})

export class ChildViewchildComponent implements OnInit {
    constructor() { }
    value = 0;
    ngOnInit() { }
}


