import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'mathRound'
})

export class DemoPipe implements PipeTransform {
    transform(value: number, isAction: boolean): any {
        if (isAction)
            return Math.round(value);
        return value;
    }
}


