import { Component, OnInit } from '@angular/core';
import { DemoPipe } from './pipe-custom-demo.pipe'
@Component({
    selector: 'pipe-custom-comp',
    template: `
        <div class='col-md-4 border-common'>
            <h4>Pipe Custom</h4>
            <p>{{number1 | mathRound : true}}</p>
            <p>{{number2 | mathRound : false}}</p>
        </div>
    `
})

export class PipeCusComponent implements OnInit {
    constructor() { }

    number1 = 1.9;
    number2 = 2.9;
    ngOnInit() {

    }
}


