import { Component, OnInit, ViewChild } from '@angular/core';
import { ChildViewchildComponent } from './child.component';

@Component({
    selector: 'parent-viewchild-component',
    template: `
    <div class='col-md-4 border-common'>
        <h4>Parent-Child Viewchild Component</h4>
        <child-viewchild-component></child-viewchild-component>
        <button class='btn btn-default' (click)='action(true)'>Add Value</button>
        <button class='btn btn-default' (click)='action(false)'>Sub Value</button>
    </div>
    `
    // <child-viewchild-component #child></child-viewchild-component>
    // <button class='btn btn-default' (click)='child.value = child.value + 1'>Add Value</button>
    // <button class='btn btn-default' (click)='child.value = child.value - 1'>Sub Value</button>
})

export class ParentViewchildComponent implements OnInit {
    constructor() { }
    @ViewChild(ChildViewchildComponent) child: ChildViewchildComponent;

    ngOnInit() {
    }
    action(bool: boolean) {
        if (bool) this.child.value++;
        else this.child.value--;
    }
}


