import { Component, OnInit } from '@angular/core';
import { Form } from '@angular/forms';
import { RequiredValidator } from '@angular/forms';
import { FormControl, FormGroup } from '@angular/forms';

@Component({
    selector: 'reactive-demo-form',
    templateUrl: './reactive-form.component.html'
})

export class ReactiveDemoFormComponent implements OnInit {
    ReactiveDemoForm: FormGroup;
    reactiveUser = '';
    reactivePass = '';
    constructor() {
        this.ReactiveDemoForm = new FormGroup({
            userName: new FormControl(),
            password: new FormControl(),
            subject: new FormGroup({
                angular: new FormControl(),
                react: new FormControl(),
                vue: new FormControl()
            })
        })
    }

    ngOnInit() { }

    noticeSuccess() {
        console.log(this.ReactiveDemoForm.value);
    }
}


