import { Component, OnInit } from '@angular/core';

@Component({
    selector: 'basic-direct-demo',
    templateUrl: 'basic-directive.component.html'
})

export class DirectDemoComponent implements OnInit {
    constructor() { }
    basicEvenDirStyle = {
        'width': '50px',
        'height': '50px',
        'border-radius': '25px',
        'background': 'skyblue'
    }
    basicOddDirStyle = {
        'width': '50px',
        'height': '50px',
        'border-radius': '2px',
        'background': 'red'
    }
    private basicDirName = '';
    ngOnInit() { }
}


