import { Component } from '@angular/core';

@Component({
    selector: 'hide-show-comp',
    templateUrl: 'hide-show-form.component.html'
})

export class HideShowDemoComponent {
    constructor() { }
    vn_word = '';
    en_word = '';
    filter = '';
    dataList = [
        {
            'en': 'action',
            'vn': 'hành động'
        },
        {
            'en': 'justice',
            'vn': 'công lí'
        },
        {
            'en': 'superman',
            'vn': 'siêu nhân'
        },
        {
            'en': 'robot',
            'vn': 'người máy'
        }]
    word_list = this.dataList;
    addVar = false;
    addNewWord() {
        if (this.vn_word && this.en_word) {
            this.word_list.push({
                'vn': this.vn_word,
                'en': this.en_word
            });
            this.en_word = '';
            this.vn_word = '';
        }
        console.log(this.word_list)
    }
    deleteWord(index) {
        this.word_list.splice(index, 1);
    }
    filterWordList() {
        if (this.filter) {
            this.word_list = [
                {
                    'en': 'justice',
                    'vn': 'công lí'
                },
                {
                    'en': 'superman',
                    'vn': 'siêu nhân'
                },
                {
                    'en': 'robot',
                    'vn': 'người máy'
                }]
        }
        else
            this.word_list = this.dataList

    }
}


