import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PageNotFoundComponent } from './component-simple/page-not-found/page-not-found.component';
import { CommonModule } from '@angular/common';
import { DetailContactComponent } from './component-simple/first-route/detail/contact-detail.component';
import { FirstRouteModule } from './component-simple/first-route/first-route.module';

const routersConfig: Routes = [
  { path: 'firstRoute/:name', component: DetailContactComponent},
  { path: '**', component: PageNotFoundComponent},//bắt lỗi ngoại lệ
];

@NgModule({
    imports: [
        RouterModule.forRoot(routersConfig),
        CommonModule,
        FirstRouteModule
    ],
    exports: [RouterModule],
    declarations: [
        PageNotFoundComponent,
        DetailContactComponent
    ],
    providers: [],
})
export class AppRoutingModule { }



