import { Component, OnInit } from '@angular/core';
import { Http } from '@angular/http';
import { WeatherDemoService } from '../../service/weather.service';

@Component({
    selector: 'weather-demo-comp',
    template: `
        <div class='col-md-4 border-common'>
            <h4>Get data Weather</h4>
            <div class='mui-textfield'>
                <input id='weather-demo' name='weather-demo' [(ngModel)]='demoChoosenCity'>
                <label for='weather-demo'>City</label>
            </div>
            <button style='margin-bottom: 15px' class='btn btn-default' (click) = 'getWeather()'>
                Get Weather
                <i *ngIf='isLoading' class='fa fa-spinner fa-spin' style='font-size: 15px'></i>
            </button>
            <div class='mui-textfield'>
                <input id='weather-demo-result' name='weather-demo-result' readonly [(ngModel)]='weaDemRes'>
                <label for='weather-demo-result'>Result</label>
            </div>
        </div>
    `,
    providers:[
        WeatherDemoService
    ]
})

export class WeatherDemoComponent implements OnInit {
    constructor(
        private http: Http,
        private weatherSv: WeatherDemoService
    ) { }
    isLoading = false;
    demoChoosenCity = '';
    weaDemRes = '';
    ngOnInit() { }
    getWeather() {
        this.isLoading = true;
        // this.http.get('http://api.openweathermap.org/data/2.5/weather?q='+this.demoChoosenCity+'&APPID=d66f9dda0eb4edbd696e861982542485')
        //     .map(res => res.json())
        this.weatherSv.getWeather(this.demoChoosenCity)
            .subscribe(
                resJson =>{
                    this.weaDemRes = resJson.weather[0].main;
                },
                error =>{
                    this.weaDemRes = "Nothing to geocode";
                }
            )
        // this.isLoading = false;
    }
}


