import { Component, OnInit } from '@angular/core';
// import { ChildDemoComponent } from './child.component'

@Component({
    selector: 'parent-demo',
    template: `
        <div class='col-md-4 border-common'>
            <h4>Parent-Child Output Component</h4>
            <p>Value: {{value}}</p>
            <child-demo (myClick)='action($event)'></child-demo>
        </div>
    `
})

export class ParentDemoComponent implements OnInit {
    constructor() { }
    value = 0;

    ngOnInit() { }
    action(isAdd) {
        if (isAdd) this.value++;
        else this.value--;
    }
}


