import { Component, OnInit, Output, EventEmitter } from '@angular/core';

@Component({
    selector: 'child-demo',
    template: `
        <button class='btn btn-default' (click)='addForParent()'>Add Value</button>
        <button class='btn btn-default' (click)='subForParent()'>Sub Value</button>
    `
})

export class ChildDemoComponent implements OnInit {
    constructor() { }
    @Output() myClick = new EventEmitter();

    ngOnInit() { }
    addForParent(){
        this.myClick.emit(true);
    }
    subForParent(){
        this.myClick.emit(false);
    }
}


